	module.exports = {"data":{"markdownRemark":{"html":"<h1>Demos (2.x)</h1>\n<table class=\"table table-striped\">\n<tr>\n<th>Demo</th><th>Source</th><th>Description</th>\n</tr>\n<tr>\n<td><a href=\"http://dashboard.hawt.io/\">dashboard.hawt.io</a></td>\n<td><a href=\"https://github.com/hawtio/hawtio-dashboard\">hawtio-dashboard</a></td>\n<td>Provides a dashboard capability; letting any angular pages be composed into a dashboard</td>\n</tr>\n<tr>\n<td><a href=\"http://forms.hawt.io/\">forms.hawt.io</a></td>\n<td><a href=\"https://github.com/hawtio/hawtio-forms\">hawtio-forms</a></td>\n<td>The forms plugin from hawtio 1.x, used to create forms from a simple schema</td>\n</tr>\n<tr>\n<td><a href=\"http://ui.hawt.io/\">ui.hawt.io</a></td>\n<td><a href=\"https://github.com/hawtio/hawtio-ui\">hawtio-ui</a></td>\n<td>The UI widgets from hawtio 1.x, including hawtio-simple-table and the editor plugin</td>\n</tr>\n</table>\n<h1>Videos (2.x)</h1>\n<p>The following videos are in the <a href=\"https://vimeo.com/album/3324251\">hawtio video album</a>:</p>\n<ul>\n<li><a href=\"https://vimeo.com/123423650\">create a hawtio plugin in 3 minutes</a></li>\n<li><a href=\"https://vimeo.com/123441243\">add hawtio forms to your plugin</a> using <a href=\"https://github.com/hawtio/hawtio-forms\">hawtio-forms</a></li>\n</ul>\n<h1>Demos (1.x)</h1>\n<ul>\n<li><a href=\"https://vimeo.com/album/2635012\" title=\"a library of demo videos using hawtio with JBoss Fuse\">various demos of using hawtio, ActiveMQ and Camel inside JBoss Fuse</a> by <a href=\"http://macstrac.blogspot.co.uk/\">James Strachan</a>. JBoss Fuse 6.1 or later uses hawtio for its management console; so all these demos are really hawtio demos (in terms of the console).</li>\n<li><a href=\"http://vimeo.com/80625940\" title=\"Demo of Fuse 6.1 with Apache Camel and hawtio on OpenShift\">Demo of Fuse 6.1 with Apache Camel and hawtio on OpenShift</a> by <a href=\"http://macstrac.blogspot.co.uk/\">James Strachan</a> showing how to get started using <a href=\"http://www.jboss.org/products/fuse\">JBoss Fuse</a> 6.1 Early Access release on OpenShift for creating integration solutions based on Apache Camel in the hybrid cloud (via <a href=\"https://www.openshift.com/products/online\">OpenShift Online</a> for the public cloud or <a href=\"https://www.openshift.com/products/enterprise\">OpenShift Enterprise</a> for on premise, or a combination of both).</li>\n<li><a href=\"https://vimeo.com/68442425\" title=\"see a demo of provisioning Fuse containers, viewing, editing, debugging and provisioning Camel routes using Fuse Fabric with the hawtio console\">CamelOne keynote 2013</a> by <a href=\"http://macstrac.blogspot.co.uk/\">James Strachan</a> showing the create, view, edit debug and provision of Camel routes using Fuse Fabric with the hawtio</li>\n<li><a href=\"https://www.youtube.com/watch?v=sL6tlEv-mxQ\">demo of FMC and hawtio with Fuse Fabric</a></li>\n</ul>\n<h2>Videos on hawtio</h2>\n<p>The following videos has been found on the web about hawtio.  If you find any others please <a href=\"http://hawt.io/community/index.html\">let us know</a> or <a href=\"https://github.com/hawtio/hawtio/issues?state=open\">raise an issue</a> and we'll add it to the list.</p>\n<ul>\n<li><a href=\"https://vimeo.com/233817010\">Hawt and spicy Java monitoring</a> by Martin Skarsaune, at <a href=\"https://2017.javazone.no/\">JavaZone 2017</a>.</li>\n<li><a href=\"https://www.youtube.com/watch?v=Bxgk9--_WzE\">hawtio - the Extensive console</a> by Stan Lewis, at <a href=\"http://www.devnation.org/\">DevNation 2014</a> presenting how you can extend and build your own plugins to hawtio. Highly recommended if you want to build custom plugins.</li>\n<li>Hawtio on Talend ESB part <a href=\"https://www.youtube.com/watch?v=lzdgxcHwfcY\">1</a>, <a href=\"https://www.youtube.com/watch?v=_vjp8rg1DNQ\">2</a>, <a href=\"https://www.youtube.com/watch?v=uJqG2JbXfkM\">3</a> by <a href=\"http://nl.linkedin.com/in/robinhuiser\">Robin Huiser</a></li>\n</ul>\n<h2>Articles on using hawtio</h2>\n<p>The following articles have been found on the web about hawtio. If you find any others please <a href=\"http://hawt.io/community/index.html\">let us know</a> or <a href=\"https://github.com/hawtio/hawtio/issues?state=open\">raise an issue</a> and we'll add it to the list.</p>\n<ul>\n<li><a href=\"http://www.bennet-schulz.com/2016/07/apache-activemq-and-hawtio.html\">Running Apache ActiveMQ and hawtio in Standalone Mode (2016)</a> by <a href=\"https://twitter.com/bennetelli\">Bennet Schulz</a></li>\n<li><a href=\"http://www.christianposta.com/blog/?p=315\">Hawtio + ActiveMQ</a> by <a href=\"http://www.christianposta.com/\">Christian Posta</a></li>\n<li><a href=\"http://iocanel.blogspot.co.uk/2013/07/hawtio-apache-jclouds.html\">Hawtio &#x26; Apache Jclouds</a> by <a href=\"http://iocanel.blogspot.co.uk/\">Ioannis Canellos</a></li>\n<li><a href=\"http://www.davsclaus.com/2013/04/apache-camel-web-dashboard-with-hawtio.html\">Apache Camel web dashboard with hawtio</a> by <a href=\"http://www.davsclaus.com\">Claus Ibsen</a></li>\n<li><a href=\"http://macstrac.blogspot.co.uk/2013/06/introducing-apache-camel-based-open.html\">introducing the Apache Camel based open source iPaaS</a> by <a href=\"http://macstrac.blogspot.co.uk/\">James Strachan</a></li>\n<li><a href=\"http://bushorn.com/monitoring-camel-application/\">Monitoring Camel Application</a> by <a href=\"http://bushorn.com/author/gnanagurus/\">Gnanaguru</a></li>\n<li><a href=\"http://blog.zenika.com/2014/01/07/hawtio-la-console-web-polyvalente\">Hawtio, la console web polyvalente</a> by Gérald Quintana (in French) with a great overview of hawtio (note: Google can translate the page into readable english)</li>\n<li><a href=\"http://blog.zenika.com/2014/01/14/hawtio-ecrire-un-plugin\">Hawtio, écrire un plugin</a> by Gérald Quintana (in French) the second part on writing a plugin (note: Google can translate the page into readable english)</li>\n<li><a href=\"http://jcordes73.blogspot.de/2014/03/hawtio-authentication-with-ldap-on.html\">Hawtio authentication with LDAP</a> by Jochen Cordes with a blog series how to use LDAP authentication with hawtio running on Tomcat 7, JBoss Fuse, and JBoss EAP.</li>\n<li><a href=\"http://sensatic.net/activemq/activemq-and-hawtio.html\">Installing hawtio as web console for ActiveMQ</a> by Dejan Bosanac shows how to install hawtio as the web console in Apache ActiveMQ. Dejan also demonstrates how you can remote connect from hawtio to any ActiveMQ broker.</li>\n<li><a href=\"http://www.christianposta.com/blog/?p=403\">HawtIO on JBoss Wildfly 8.1 -- step by step</a> by <a href=\"http://www.christianposta.com/\">Christian Posta</a> How to get HawtIO running and secured on JBoss Wildfly Application Server 8.1</li>\n<li><a href=\"http://jimmidyson.github.io/hawtio-on-OpenShift/\">HawtIO on OpenShift</a> by Jimmi Dyson, how to run Apache Tomcat on OpenShift Online and install hawtio into the Tomcat.</li>\n<li><a href=\"http://jcordes73.blogspot.de/2014/12/deploying-hawtio-on-jboss-eap-6.html\">Hawtio on JBoss EAP 6</a> by Jochen Cordes how to deploy Hawtio on JBoss EAP 6</li>\n</ul>\n<h2>Developer articles</h2>\n<ul>\n<li><a href=\"http://www.wayofquality.de/open%20source/hawtio/creating-a-hwatio-directive/\">Creating a directive in hawtio</a> by <a href=\"http://www.wayofquality.de/index.php/blog\">Andreas Gies</a></li>\n<li><a href=\"http://www.wayofquality.de/open%20source/hawtio/using-a-datafactory-in-hawtio/\">Using a data factory to encapsulate backend data in hawtio</a> by <a href=\"http://www.wayofquality.de/index.php/blog\">Andreas Gies</a></li>\n</ul>","frontmatter":{"title":""}}},"pathContext":{"slug":"/docs/articles/"}}

